body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body button:hover {
  cursor:pointer;
}

body button:disabled:hover {
  cursor:default;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.task-item {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.completed {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 5px;
    margin-right: 10px;
    width: 8%;
    min-width: 100px;
}

.custom-control-label::after {
    cursor: pointer;
}

.custom-control-label {
    cursor: pointer;
}

.task-info {
    display: flex;
    flex-direction: column;
    width: 84%;
    padding-top: 10px;
}

.task-info-date-prio {
    display: flex;
    flex-direction: row;
    padding-bottom: 10px;
}

.delete-task {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-right: 5px;
    margin-left: 10px;
    width: 8%;
}

.delete-task button {
    width: 80px;
    height: 80px;
    font-size: 40px;
    padding: 0;
    margin: 0;
}

.task-name {
    margin: 0;
}

.task-description-wrapper {
    width: 100%;
    height: 100%;
    padding-top: 10px;
}

.task-description {
    width: 100%;
    resize: none;
    height: 150px;
    padding: 5px;
    padding-top: 0px;
    font-size: 20px;
}

textarea:disabled {
    background-color: white;
    color: black;
    border: 1px solid black;
}
.list-of-tasks {
    padding: 66px;
    padding-top: 10vh;
    height: 100%;
    width: 100%;
}

.moving-task-in-list-enter {
    opacity: 0.01;
  }
  
  .moving-task-in-list-enter.moving-task-in-list-enter-active {
    opacity: 1;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
  }
  
  .moving-task-in-list-leave {
    opacity: 1;
  }
  
  .moving-task-in-list-leave.moving-task-in-list-leave-active {
    opacity: 0.01;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
  }
.header {
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 1;
}

.header-wrapper {
    width: calc(100% - 15px);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 50px;
    padding-right: 50px;
}

.btn-group a:not(:last-child) .btn {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    width: 120px;
}

.btn-group a+a .btn {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    width: 120px;
}

.sorting-elements {
    display: flex;
    flex-direction: row;
}

.sorting-switcher:hover {
    cursor: pointer;
}

.sorting-switcher {
    color: rgb(219, 143, 29);
}

.active-switcher {
    color: rgb(17, 168, 17);
}
.new-task-overlay {
    position: fixed;
    top: 0;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    visibility: hidden;
    -webkit-transition: visibility 0.4s;
    transition: visibility 0.4s;
}

.active {
    visibility: visible;
}

.new-task-window {
    position: relative;
    width: 100%;
    top: 100%;
    -webkit-transition: top 0.5s;
    transition: top 0.5s;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.new-task-window-content {
    width: 700px;
    height: 400px;
}

.name-input {
    width: 300px;
    padding-left: 10px;
    border-radius: 0.25rem;
}

[type=text].form-control:not(textarea) {
    height: 50px;
}

.appear {
    top: 0;
}

.new-task-time {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.new-task-desc {
    resize: none;
    padding-left: 10px;
    padding-top: 10px;
    border-radius: 0.25rem;
}

textarea.form-control {
    height: 200px;
}

button {
    height: 50px;
}

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-btn {
    width: 120px;
    box-shadow: 0 0 0 0;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 120px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.dropdown-content div {
    color: black;
    padding: 12px 16px;
    display: block;
    font-size: 0.8125rem;
}

.dropdown-content div:first-child {
    border-top-right-radius: 0.25rem;
    border-top-left-radius: 0.25rem;
}

.dropdown-content div:last-child {
    border-bottom-right-radius: 0.25rem;
    border-bottom-left-radius: 0.25rem;
}

.dropdown-content div:hover {
    background-color: #ddd;
    cursor: pointer;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.error {
    border: 1px solid red;
}

.done {
    border: 1px solid rgb(50, 212, 50);
}
